import { Plugin } from 'vue';
import { io, Socket } from 'socket.io-client';

declare module '@vue/runtime-core' {
    interface ComponentCustomProperties {
        $socket: Socket;
        $socketError: boolean;
    }
}

const apiBaseUrl = window.MAIN_WEBSOCKET_IP || 'http://192.168.0.131:3002';

const socketPlugin: Plugin = {
    install: (app) => {
        const socket: Socket = io(`${apiBaseUrl}`, {
            transports: ['websocket'],
            withCredentials: true,
            reconnection: true,            //   
            reconnectionAttempts: 15,      //   
            reconnectionDelay: 3000,       //    (3)
            timeout: 30000,                //     (30)
        });

        //  ping     
        let pingTimeout: NodeJS.Timeout | null = null;

        //     
        socket.on('connect', () => {
            console.log('WebSocket connected');
            app.config.globalProperties.$socketError = false;
            sendPing();
            if (pingTimeout) {
                clearTimeout(pingTimeout);
                pingTimeout = null;
            }
        });

        //     
        socket.on('disconnect', () => {
            console.log('WebSocket disconnected');
            app.config.globalProperties.$socketError = true;
            console.log(' .  .');
            sendPing();
            if (pingTimeout) {
                clearTimeout(pingTimeout);
                pingTimeout = null;
            }
        });

        //      
        socket.on('connect_error', (error) => {
            console.error('WebSocket connection error:', error);
            app.config.globalProperties.$socketError = true;
            sendPing();
            if (pingTimeout) {
                clearTimeout(pingTimeout);
                pingTimeout = null;
            }
            console.log('   .  .');
        });

        socket.on('pong', (latency) => {
            console.log(`Pong received with latency: ${latency}ms`);
        });

        function sendPing() {
            if (socket.connected) {
                socket.emit('ping');  //  ping  
                console.log('Ping sent to server');
            }

            //  ping  
            pingTimeout = setTimeout(sendPing, 10000); // 10   ping 
        }

        //     
        app.config.globalProperties.$socket = socket;
        app.config.globalProperties.$socketError = false; //     

        // Vue      
        const originalUnmount = app.unmount.bind(app);

        app.unmount = () => {
            socket.disconnect(); //   
            if (pingTimeout) {
                clearTimeout(pingTimeout); //    pingTimeout 
            }
            originalUnmount(); //  unmount 
        };
    },
};

export default socketPlugin;
